<template>
  <div id="reform">
    <p @click="formTable">dsada</p>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 're-form',
  data () {
    return {

    }
  },
  methods: {
    formTable () {
      $.ajax({
        async: false,
        url: '/establish_form/',
        type: 'POST',
        // eslint-disable-next-line no-undef
        data: {display_requirement: '*', csrfmiddlewaretoken: csrf_token},
        success: function (data) {
          if (data === 'success') {
            $('body').append('<a href=\'download_form\' id=\'download_form\'>下载链接</a>')
            $('#download_form')[0].click()
            $('#download_form').remove()
          }
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



